import React from 'react';
import ReactDOM from 'react-dom';
import { Route, BrowserRouter as Router } from 'react-router-dom'
import './index.css';
import App from './App'
import Landing from './views/Landing';
import ViewRecordings from './views/ViewRecordings';
import PlayRecording from './views/PlayRecording';
import SaveSuccess from './views/SaveSuccess';
import * as serviceWorker from './serviceWorker';

const routes = (
  <Router>
    <div>
      <Route exact path="/" component={Landing} />
      <Route path="/rec/:name" component={App} />
      <Route exact path="/list/:name" component={ViewRecordings} />
      <Route path="/list/:name/:audioName" component={PlayRecording} />
      <Route path="/save/success" component={SaveSuccess} />
    </div>
  </Router>
)

ReactDOM.render(routes, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
