import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import Text from '../utils/Text';
import DetailsForm from '../components/DetailsForm';
import AudioClip from '../components/AudioClip'

class ViewRecordings extends Component {

  state = {
    name: null,
    recList: null,
  }

  componentDidMount() {
    const name = this.props.match.params.name;
    this.setState({name});

    fetch(`/rec/list/${name}`).then(res => {
      return res.json();
    }).then(data => {
      this.setState({ recList: data });
    })
  }

  onRecClick = (e, audioName) => {
    e.preventDefault();

    const { name } = this.state;

    this.props.history.push(`/list/${name}/${audioName}`);
  }

  render() {

    const { classes } = this.props;

    const {
      name,
      recList
    } = this.state;

    return(
      <div className={classes.container}>
        <div className={classes.details}>
          <div className={classes.greeting}>
            <Text heading>
              Recordings
            </Text>
            <div className={classes.subheading}>
              <Text>
                Click to play the recording
              </Text>
            </div>
          </div>
          {recList && (
            <div className={classes.list}>
              {
                recList.map(elem => {
                  return (<AudioClip audioName={elem.audioName} onRecClick={this.onRecClick} />)
                })
              }
            </div>
        )}
        </div>
      </div>
    );
  }
}

export default withStyles({
  '@global': {
    html: {
      height: "100%",
    },
    body: {
      height: "100%",
    },
  },
  container : {
    position: "absolute",
    height: "100%",
    width: "100%",
  },
  greeting: {
    textAlign: "center",
    position: "relative",
    left: "26%",
    top: "2%",
    width:"40%",
    padding: "1.2em",
  },
  details : {
    position: "relative",
    height: "80vh",
    left: "0vw",
    top: "8vh",
    width: "100vw",
    background : "-webkit-linear-gradient(left, #CA464B, #9F00EA)",
    borderRadius: "0%",
  },
  subheading: {
    width: "100%",
    position: "relative",
  },
  list: {
    position:"relative",
    height: "50%",
    width: "35%",
    top:"5vh",
    left: "30%",
    overflow: "auto",
  },
})(ViewRecordings);
