import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';

class Text extends Component {

  textStyle = () => {
    const { heading, subHeading, classes } = this.props;

    if (heading)
    return classes.heading;
    else if (subHeading)
    return classes.subheading;
    else
    return classes.body;
  };

  render() {
    
    return (
      <div className={this.textStyle()} {...this.props} />
    )
  }
}

export default withStyles({
  heading : {
    fontFamily: 'Quattrocento, serif',
    fontWeight: 800,
    fontSize:"4em",
    letterSpacing: "-0.03em",
    color:"#ffffff",
    margin: "0.3em",
  },
  body : {
    fontFamily: 'Roboto, serif',
    fontWeight: 300,
    fontSize:"1em",
    color:"#ffffff",
  },
  subheading : {
    fontFamily: 'Quattrocento, serif',
    fontWeight: 600,
    fontSize:"2em",
    letterSpacing: "-0.03em",
    color:"#ffffff",
    margin: "0.3em",
  },
})(Text);
