import React, { Component, Fragment } from 'react';
import { withStyles } from '@material-ui/styles';
import Text from '../utils/Text';

class Projector extends Component {

  state = {
    /* input paragraph text formatted in array of sentences */
    formattedText: [],
    /* index to traverse through the paragraph */
    index: 0,
    timer: null,
    isOver: false,
    isOngoing: false,
    hasStarted: false
  }

  componentDidMount() {
    const { text } = this.props;
    if(text) {
      this.formatText();
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.isRecording !== prevState.isRecording) {
      return ({ isRecording: nextProps.isRecording });
    }
    else return null;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.isRecording && prevProps.isRecording !== this.props.isRecording) {
      this.startProjecting();
    }
  }

  nextLine = () => {

      const { index, formattedText } = this.state;
      
      if(index < formattedText.length)
      {
        this.setState(prevState => ({
          index: prevState.index + 1
        }));
      }
      else {
        this.stopProjecting();
      }
  }

  startProjecting = () => {
    if(this.props.isRecording){
      setInterval(() => {
        this.nextLine();
      },3000);
    }
  };

  stopProjecting = () => {
    clearInterval(this.state.timer);
    this.setState({
      isOver: true,
    });
  }

  formatText = () => {
    const { text } = this.props;

    let formattedText = text.split('.');
    formattedText.pop();

    this.setState({ formattedText });
  }

  render() {

    const { name, classes, isRecording } = this.props;

    const { formattedText, index, isOver } = this.state;

    return (
      <Fragment>
        { (!isOver && isRecording) && (
          <div className={classes.projector}>
            <Text subHeading>{formattedText[index]}</Text>
          </div>
        )}
        { (isOver) && (
          <div className={classes.projector}>
            <Text heading>Thank you!</Text>
            <Text body> Please press the stop button to proceed </Text>
          </div>
        )}
        { (!isRecording && !isOver) && (
          <div className={classes.projector}>
            <Text heading>Hi {name}!</Text>
            <Text body> Please press the start button to begin </Text>
          </div>
        )}
      </Fragment>
    )
  }
}

export default withStyles({
  projector : {
    textAlign: "center",
  }
})(Projector);
