import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import MusicCircleOutlineIcon from 'mdi-react/MusicCircleOutlineIcon'


class AudioClip extends Component {

  onRecClick = e => {
    const { onRecClick, audioName } = this.props;

    onRecClick(e, audioName);
  }

  render() {
    const { classes, audioName } = this.props;

    return(
      <div className={classes.container} onClick={this.onRecClick}>
        <MusicCircleOutlineIcon className={classes.icon} color="black" size="50"/>
        <span className={classes.name}>{audioName}</span>
      </div>
    );
  }
}

export default withStyles({
  container: {
    position: "relative",
    height:"25%",
    width: "55%",
    left:"22.5%",
    background: "white",
    border: "1px white solid",
    borderRadius: "1%",
    opacity: "0.4",
    transition: "300ms all ease",
    cursor: "pointer",
    marginBottom: "4%",
    '&:hover': {
      opacity: "0.8"
    }
  },
  icon: {
    position: "relative",
    left: "3%",
    top: "12%",
  },
  name: {
    position: "absolute",
    left: "27%",
    top: "36%",
  }
})(AudioClip);
