import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import Text from '../utils/Text';
import SaveForm from '../components/SaveForm';

class Save extends Component {

  onRecSave = (e, name) => {
    this.props.onRecSave(e, name)
  }

  onRecAgain = (e) => {
    this.props.onRecAgain(e);
  }

  render() {

    const { classes } = this.props;

    return(
      <div className={classes.container}>
        <div className={classes.details}>
          <div className={classes.greeting}>
            <Text heading>
              Good job!
            </Text>
            <div className={classes.subheading}>
              <Text>
                Do you wish to save the recording?
              </Text>
            </div>
          </div>
          <div className={classes.form}>
            <SaveForm
              onRecSave={this.onRecSave}
              onRecAgain={this.onRecAgain}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles({
  '@global': {
    html: {
      height: "100%",
    },
    body: {
      height: "100%",
    },
  },
  container : {
    position: "absolute",
    height: "100%",
    width: "100%",
  },
  greeting: {
    textAlign: "center",
    position: "relative",
    left: "26%",
    top: "12vh",
    width:"40%",
    padding: "1.2em",
  },
  details : {
    position: "relative",
    height: "80vh",
    left: "0vw",
    top: "8vh",
    width: "100vw",
    background : "-webkit-linear-gradient(left, #CA464B, #9F00EA)",
    borderRadius: "0%",
  },
  subheading: {
    width: "100%",
    position: "relative",
  },
  form: {
    position:"relative",
    height: "50%",
    width: "35%",
    top:"14vh",
    left: "32%",
  },
})(Save);
