import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';

class AudioVisualiser extends Component {

  state = {
    canvas: React.createRef(),
  }

  componentDidUpdate() {
    this.draw();
  }

  draw() {
    /* Drawing on the canvas */
    const { audioData } = this.props;
    const canvas = this.state.canvas.current;
    const height = canvas.height;
    const width = canvas.width;
    const context = canvas.getContext('2d');
    let x = 0;
    const step = (width * 1.0) / audioData.length;

    let gradient = context.createLinearGradient(0, 0, 800, 0);
    gradient.addColorStop(0, "#CA464B");
    gradient.addColorStop(0.5, "#FFFFFF");
    gradient.addColorStop(1, "#9F00EA");

    context.lineWidth = 1.5;
    context.strokeStyle = gradient;

    context.clearRect(0, 0, width, height);

    // Start drawing
    context.beginPath();
    context.moveTo(0, height / 2);

    for (const item of audioData) {
      const y = (item / 255.0) * height;
      context.lineTo(x, y);
      x += step;
    }
    context.lineTo(x, height / 2);
    context.stroke();
}

  compon

  render() {

    const {
      classes,
    } = this.props;

    return (
      <canvas className={classes.idle} width="800" height="300" ref={this.state.canvas}/>
      //<canvas className={classes.idle} width="1200" height="300" ref={this.state.backCanvas}/>
    )
  }
}

export default withStyles({
  idle: {
    height: "200px",
    background: "transparent",
    width: "100%",
  }
})(AudioVisualiser);
