import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';

class RecordButton extends Component {
  render() {
    const {classes, isRecording } = this.props
    return(
      //<RecordIcon className = {classes.root} size={size} />
      <div className={classes.root} onClick={this.props.onClick}>
        <div className={isRecording ? classes.stop : classes.start}></div>
      </div>
    );
  }
}

export default withStyles({
  root: {
    border: '1px #ffffff solid',
    borderRadius: '50%',
    color: '#ffffff',
    height: '55px',
    width: '55px',
    cursor: 'pointer',
  },
  start: {
    position: 'relative',
    background : '#ffffff',
    height: '86%',
    width: '86%',
    borderRadius: '50%',
    top: '7%',
    left: '7%',
    transition: '100ms all ease',
  },
  stop: {
    position: 'relative',
    background : '#ffffff',
    height: '40%',
    width: '40%',
    top:'30%',
    left:'30%',
    borderRadius: "20%",
    transition: '100ms all ease',
  }
})(RecordButton);
