import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import Text from '../utils/Text';
import DetailsForm from '../components/DetailsForm';
import PlayButton from '../components/PlayButton';
import AudioClip from '../components/AudioClip'
import AudioAnalyser from '../components/AudioAnalyser';

class ViewRecordings extends Component {

  state = {
    name: null,
    audioName: null,
    audio: null,
    audioUrl: null,
    isPlaying: false,
  }

  componentDidMount() {
    const {
      name,
      audioName
    } = this.props.match.params;

    this.setState({
      name,
      audioName,
    });

    this.fetchAudio()
      .then(response => {
        var blob = new Blob([response.value], { type: 'audio/ogg' });
        var url = URL.createObjectURL(blob)
        this.setState({
          audioUrl: url,
        });
      })
  }

  onListButtonClick = e => {
    e.preventDefault();

    const {
      name
    } = this.state;

    this.props.history.push(`/list/${name}`);
  }

  fetchAudio = () => {
    const {
      name,
      audioName
    } = this.props.match.params;

    return fetch(`/rec/list/${name}/${audioName}`).then(res => {
      let reader = res.body.getReader();
      return reader.read().then(result => {
        return result;
      })
    });
  }

  onPlay = () => {
    const { audioUrl, isPlaying } = this.state;

    if(isPlaying === false){
      this.setState({ isPlaying: true });
      let audio = new Audio();
      audio.src = audioUrl;
      audio.play();
      audio.addEventListener('ended', e => {
        audio.currentTime =0;
        this.setState({
          isPlaying: false,
          audio: null,
        });
      })
      this.setState({ audio });
    } else {
      let { audio } = this.state;

      audio.pause();
      this.setState({
        isPlaying: false,
        audio: null,
      });

    }
  }

  render() {

    const { classes } = this.props;

    const {
      name,
      isPlaying,
      audioName,
      audio
    } = this.state;

    return(
      <div className={classes.container}>
        <div className={classes.details}>

          <button className={classes.but} onClick={this.onListButtonClick}>
            My Recordings
          </button>

          <div className={classes.greeting}>
            <Text heading>
              Now Playing
            </Text>
            <div className={classes.subheading}>
              <Text>
                {audioName}
              </Text>
            </div>
          </div>

          { audio && (
            <div className={classes.analyser}>
              <AudioAnalyser audio={audio} />
            </div>
          )}

          <div className={classes.controls}>
            <PlayButton onClick={this.onPlay} isPlaying={isPlaying}/>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles({
  '@global': {
    html: {
      height: "100%",
    },
    body: {
      height: "100%",
    },
  },
  container : {
    position: "absolute",
    height: "100%",
    width: "100%",
  },
  greeting: {
    textAlign: "center",
    position: "relative",
    left: "26%",
    top: "2%",
    width:"40%",
    padding: "1.2em",
  },
  details : {
    position: "relative",
    height: "80vh",
    left: "0vw",
    top: "8vh",
    width: "100vw",
    background : "-webkit-linear-gradient(left, #CA464B, #9F00EA)",
    borderRadius: "0%",
  },
  subheading: {
    width: "100%",
    position: "relative",
  },
  controls: {
    position: "absolute",
    top: "80%",
    left: "47%"
  },
  analyser: {
    position: "absolute",
    top:"40%",
    left:"10%",
    width:"80%"
  },
  but : {
    border: "none",
    position: "absolute",
    width: "11%",
    height: "10%",
    left: "87.3%",
    top: "5%",
    background: "transparent",
    borderRadius: "3%",
    border: "1px solid white",
    color: "white",
    fontFamily:"Roboto",
    cursor: "pointer",
    transition: "300ms all ease",
    opacity: "0.8",
    '&:hover': {
      opacity: '0.9',
      background: "#ffffff",
      color: "black",
    }
  }
})(ViewRecordings);
