import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import TextField from '@material-ui/core/TextField';

class SaveForm extends Component {

  state = {
    /* Recording name*/
    name: "",
  }

  onRecSave = e => {
    const { name } = this.state;
    this.props.onRecSave(e, name);
  }

  onRecAgain = e => {
    this.props.onRecAgain(e);
  }

  handleInputChange = e => {
    this.setState({ name: e.target.value });
  }

  render() {
    const { classes } = this.props;
    const { name } = this.state;

    return(
      <form className={classes.container}>
        <TextField
          className={classes.root}
          variant="outlined"
          label="Name"
          value={name}
          onChange={this.handleInputChange}
        />
      <button className={classes.save} onClick={this.onRecSave}>Save Audio</button>
      <button className={classes.cancel} onClick={this.onRecAgain}>Record Again</button>
    </form>
    );
  }
}

export default withStyles({
  container : {
    width: "100%",
    height: "100%",
  },
  root: {
    borderColor:"red",
    width: "90%",
  },
  save : {
    border: "none",
    position: "relative",
    width: "30%",
    height: "17%",
    left: "10%",
    top: "13%",
    background: "#ffffff",
    cursor: "pointer",
    borderRadius: "3%",
    fontFamily:"Roboto",
    transition: "300ms all ease",
    opacity: "0.6",
    '&:hover': {
      opacity: '0.9',
    }
  },
  cancel : {
    border: "none",
    position: "relative",
    width: "30%",
    height: "17%",
    left: "19%",
    top: "13%",
    background: "#ffffff",
    borderRadius: "3%",
    fontFamily:"Roboto",
    cursor: "pointer",
    transition: "300ms all ease",
    opacity: "0.6",
    '&:hover': {
      opacity: '0.9',
    }
  }
})(SaveForm);
