import React, {Component, Fragment} from 'react';
import {withStyles} from '@material-ui/styles';
import './App.css';
import RecordButton from './components/RecordButton';
import Projector from './components/Projector'
import Save from './views/Save';
import sampleString from './utils/sampleString';
import AudioAnalyser from './components/AudioAnalyser';
import Text from './utils/Text';

class App extends Component {

  state = {
    mediaRecorder: null,
    isRecording: false,
    isOver: false,
    audioChunks: [],
    recAudio: null,
    name: null,
    stream: null,
  }

  componentDidMount() {
    const name = this.props.match.params.name;
    this.setState({name});
  }

  onRecSave = async (e, audioName) => {
    e.preventDefault();

    const {audioChunks, name} = this.state;

    let saveData = new FormData();

    const recAudio = new Blob(audioChunks, {'type': 'audio/ogg; codecs=opus'});

    saveData.append('recAudio', recAudio);
    saveData.append('name', name);
    saveData.append('audioName', audioName);

    const options = {
      method: "POST",
      body: saveData
    }

    fetch('/rec/save', options).then(res => {
      if (res.status === 200) {
        this.props.history.push('/save/success');
      }
    });
  }

  onRecAgain = e => {
    e.preventDefault();

    this.setState({
      isOver: false,
      stream: null,
      audioChunks: [],
    });
  }

  onListButtonClick = e => {
    e.preventDefault();

    const {
      name
    } = this.state;

    this.props.history.push(`/list/${name}`);
  }

  initRecorder = async () => {
    await navigator.mediaDevices.getUserMedia({audio: true}).then(stream => {
      this.setState({
        mediaRecorder: new MediaRecorder(stream),
        stream,
      });
    });

    const {audioChunks, mediaRecorder} = this.state;

    // Register recording functions
    mediaRecorder.ondataavailable = (e) => {
      audioChunks.push(e.data);
    }

    mediaRecorder.onstop = (e) => {
      const {audioChunks} = this.state;

      const recAudio = new Blob(audioChunks, {'type': 'audio/ogg; codecs=opus'});

      this.setState({recAudio: recAudio, isRecording: false});
    }
  };

  startRecorder = () => {
    let {mediaRecorder} = this.state;
    mediaRecorder.start(1000)
    this.setState({isRecording: true});
  }

  stopRecorder = () => {
    let {mediaRecorder} = this.state;

    this.setState({isRecording: false, isOver: true});

    mediaRecorder.stop();
  }

  handleRecordingClick = async () => {
    const {isRecording} = this.state;

    if (!isRecording) {
      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        await this.initRecorder();
        this.startRecorder();
      }
    } else {
      this.stopRecorder();
    }
  };

  render() {

    const {
      isRecording,
      recAudio,
      isOver,
      name,
      stream,
    } = this.state;

    const {classes} = this.props;

    return (
      <Fragment>
      {
        !isOver
          ? (
          <div className={classes.container}>

            { !isRecording && (
              <button className={classes.but} onClick={this.onListButtonClick}>
                My Recordings
              </button>
            )}

            <div className={classes.projector}>
              <Projector text={sampleString} name={name} isRecording={isRecording}/>
            </div>

            { stream && (
              <div className={classes.analyser}>
                <AudioAnalyser audio={stream} />
              </div>
            )}

            <div className={classes.controls}>
              <RecordButton onClick={this.handleRecordingClick} isRecording={isRecording}/>
            </div>
          </div>)
          : (<div>
            <Save recAudio={recAudio} onRecAgain={this.onRecAgain} onRecSave={this.onRecSave}/>
          </div>)
      }
      {
        /* uncomment the following lines to listen to the audio recorded */

         // recAudio && (<audio controls="controls">
         //   <source src={URL.createObjectURL(recAudio)} type="audio/ogg"/>
         // </audio>)
      }
    </Fragment>);
  }
}

export default withStyles({
  '@global': {
    html: {
      height: "100%"
    },
    body: {
      height: "100%"
    }
  },
  container: {
    position: "absolute",
    width: "100%",
    height: "80%",
    top: "8%",
    background: "-webkit-linear-gradient(left, #CA464B, #9F00EA)"
  },
  controls: {
    position: "absolute",
    top: "80%",
    left: "47%"
  },
  projector: {
    position: "absolute",
    top: "10%",
    left: "10%",
    width: "80%",
  },
  analyser: {
    position: "absolute",
    top:"40%",
    left:"10%",
    width:"80%"
  },
  but : {
    border: "none",
    position: "absolute",
    width: "11%",
    height: "10%",
    left: "87.3%",
    top: "5%",
    background: "transparent",
    borderRadius: "3%",
    border: "1px solid white",
    color: "white",
    fontFamily:"Roboto",
    cursor: "pointer",
    transition: "300ms all ease",
    opacity: "0.8",
    '&:hover': {
      opacity: '0.9',
      background: "#ffffff",
      color: "black",
    }
  }

})(App);
