import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';

class PlayButton extends Component {
  render() {
    const {classes, isPlaying } = this.props
    return(
      <div className={classes.root} onClick={this.props.onClick}>
        <div className={isPlaying ? classes.stop : classes.start}></div>
      </div>
    );
  }
}

export default withStyles({
  root: {
    border: '1px #ffffff solid',
    borderRadius: '50%',
    color: '#ffffff',
    height: '55px',
    width: '55px',
    cursor: 'pointer',
  },
  start: {
    position: 'relative',
    background : 'transparent',
    boxSizing: 'border-box',
    border: 'solid',
    borderColor: 'transparent transparent transparent white',
    borderWidth: '10px 0px 10px 20px',
    borderRadius: "15%",
    height: '40%',
    width: '40%',
    top: '30%',
    left: '34%',
    transition: '200ms all ease',
  },
  stop: {
    position: 'relative',
    background : '#ffffff',
    height: '40%',
    width: '40%',
    top:'30%',
    left:'30%',
    borderRadius: "20%",
    transition: '200ms all ease',
  }
})(PlayButton);
